$background: #434343;

.App {
  width: 100%;
  height: 100vh;
  background-color: $background;
  text-align: center;
  padding-top: 50px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
